@import url('https://fonts.googleapis.com/css2?family=Rubik+Mono+One&display=swap');

@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap');

.header-main{width: 100%;position:relative;
	height: auto;clip-path: polygon(100% 0, 100% 87%, 100% 87%, 0 100%, 0 0);

}


.header{
    width: 100%;position:relative;



    & .in-header{position: relative;  
    	  	height:110vh;
    	width: 100%;background-position: right;
    	min-height: 500;background-color: black;
    	background-image:linear-gradient(90deg, rgba(0,0,0,0.5633850952490371) 0%, rgba(2,2,2,0.5353738907672444) 100%), url(../../../images/image2.png);
        
    	background-size: contain;    	background-repeat: no-repeat;

     
    

            & .header-text{position: absolute;top: 40%;
            	left: 50%;
            	transform: translate(-50%,-50%);color: white;
            	font-size: 90px;
            	
            	margin: auto;text-transform: uppercase;letter-spacing: 8px;
            	background-image: url(../../../images/f-logo2.svg);
            	background-size: contain; 
                background-repeat: repeat; 
                -webkit-background-clip: text; 
                -webkit-text-fill-color: transparent; 
                 
         
          
      
                -webkit-font-smoothing: antialiased;
                -webkit-text-stroke-width: 1px;
                -webkit-text-stroke-color: white;font-family: 'Rubik Mono One', sans-serif;

            }

                & .header-text::after{
              
                    	content: 'crossfit . working harder';display: block;
                        font-size: 10px;font-family: 'Poppins', sans-serif;
                        background-image: none;letter-spacing: 6px;text-align: center;transform: scale(1);
                        
                    	
                    	
                    

                   }

                 & .header-text::before{
                        	content: 'Fitness';display: block;
                            font-size: 65px;
                        	
                        	margin: auto;text-transform: uppercase;letter-spacing: 7px;transform: scale(0.99,1.4);
                        	background-image: url(../../../images/f-logo2.svg);
                        	background-size: cover;background-position: left; 
                            background-repeat: repeat; 
                            -webkit-background-clip: text; 
                            -webkit-text-fill-color: transparent; 
                         
                         
                          
                  
                            -webkit-font-smoothing: antialiased;
                           -webkit-text-stroke-width: 1px;
                            -webkit-text-stroke-color: white;font-family: 'Rubik Mono One', sans-serif;
                    	
                    	}
                    

    }


}

































